import { Component, OnInit,Input,Output,EventEmitter } from '@angular/core';


@Component({
  selector: 'app-bd-tool-bar',
  templateUrl: './bd-tool-bar.component.html',
  styleUrls: ['./bd-tool-bar.component.css']
})
export class BdToolBarComponent implements OnInit {

  @Output()
  operate:EventEmitter<OperateEnum> = new EventEmitter();

  constructor() { }

  ngOnInit() {
  }

  onButtonClick(ope:OperateEnum){
    this.operate.emit(ope);
  }
}

export enum OperateEnum{
    //依次是：新增，修改，删除，作废，取消作废，审核，取消审核，打印，导出
    insert,update,delete,obsolete,cancelObsolete,audit,cancelAudit,print,export
}